<template>
    <div id="secondCategory" class="flexAlignCenter ">
        <ul class="textCenter">
            <li v-for="item,index in categoryList" :class="item.active?'active':''"  @click.stop="itemTap(item)">
                <i class="iconfont icon-jiantou" v-if="item.active"></i>
                <i :class="'iconfont '+item.icon"></i>
                <span class="cateName">{{item.name}}</span>
            </li>
            <!-- <li>
                 <i class="iconfont icon-gongyipin"></i>
                <span class="cateName">工艺品</span>
            </li> -->
        </ul>
    </div>
</template>

<script>
export default {
data(){
    return {
        categoryList:[
            {
                icon:'icon-icon-test',
                name:'人物',
                active:true
            },{
                icon:'icon-huahui',
                name:'花卉',
                active:false
            },{
                icon:'icon-shanshui',
                name:'山水',
                active:false
            },{
                icon:'icon-huangguan',
                name:'吉瑞',
                active:false
            },{
                icon:'icon-dongwu',
                name:'动物',
                active:false
            },{
                icon:'icon-icon--',
                name:'字画',
                active:false
            },{
                icon:'icon-B-zhexuezongjiao',
                name:'宗教',
                active:false
            },
        ]
    }
},
methods:{
      itemTap(item){
          for(var i in this.categoryList){
              this.categoryList[i].active=false;
          }
          item.active=true;
      }
  }
}
</script>

<style scoped>
#secondCategory{
    width: 70%;
}
#secondCategory ul{
    list-style: none;
   padding-left: 0;
    width: 100%;
}
#secondCategory ul li{
   color: #333;
    margin: 20px 0;
    text-align: left;
    padding: 20px 0;
     padding-left:40px;
}
#secondCategory i.iconfont{
   
}
#secondCategory ul li.active{
    color: #b3a777;
    border-bottom: 1px solid;
    padding-left: 30px;
}
#secondCategory ul li.active i.icon-jiantou{
    margin-right: 30px;
}

</style>



