<template>
  <div id="category">



    <!-- 内容开始-->
    <div class="catalog flexStart bgw">
      <firstCategory></firstCategory>
      <secondCategory></secondCategory>
    </div>

    <div class="content flexAlignCenter">
      <div class="mainContent">
          <div class="eachPic" v-for="item,index in imgList" @click="gotoSeeDetailPic">
              <img :src="item.src" alt="">
          </div>
      </div>
    </div>




  </div>
</template>

<script>
import fixText from "@/components/fixText";
import textLogo from "@/components/textLogo";
import secondCategory from "@/components/secondCategory";
import firstCategory from "@/components/firstCategory";
export default {
  name: "category",

  data() {
    return {
     
      imgList: [
        {
          id: 1,
          src: require("../../assets/images/category2/pic1.jpg")
        },
        {
          id: 2,
          src: require("../../assets/images/category2/pic1.jpg")
        },
        {
          id: 3,
          src: require("../../assets/images/category2/pic1.jpg")
        },
        {
          id: 4,
          src: require("../../assets/images/category2/pic1.jpg")
        },
        {
          id: 5,
          src: require("../../assets/images/category2/Lpic1_03.jpg")
        },
        {
          id: 6,
          src: require("../../assets/images/category2/Lpic1_03.jpg")
        },
        {
          id: 7,
          src: require("../../assets/images/category2/pic1.jpg")
        },
        {
          id: 8,
          src: require("../../assets/images/category2/Lpic1_03.jpg")
        }
      ]
    };
  },

  components: {
    secondCategory,
    fixText,
    firstCategory,
    textLogo
  },

  methods: {
    gotoSeeItems() {
      this.$router.push({
        path: "/categoryItems"
      });
    },
    gotoSeeDetailPic(){
       this.$router.push({
        path: "/detailPic"
      });
    }
  }
};
</script>

<style scoped>
#category {
  background: #fcfafb;
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.catalog {
  width: 30%;
  height: 100%;
}

#category .mainContent {
  background: #f3f2f7;
  align-content: flex-start;
  display: flex;
  flex-wrap: wrap;
  width: 566px;
  padding-left: 36px;
  padding-top: 36px;
  padding-bottom: 36px;
  width: 80%;
  height: 90%;
  margin: 0 auto;
  /* overflow: scroll; */
}

#category .mainContent .eachPic {
  height: 103px;
  max-width: 246px;
  margin-right: 36px;
  margin-bottom: 40px;
}
/*
  #category .mainContent .eachPic img {
    width: auto;
    height: 100%;
  } */

#category .content {
  width: 70%;
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#category .eachBox {
  /* 先不显示 */
  display: none;
}

#category .content .title {
  color: #b3a777;
  font-size: 24px;
  letter-spacing: 5px;
}

#category .categoryList {
  padding-left: 0;
  list-style: none;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

#category .eachCategory {
  display: inline;
  width: 100%;
}

#category .categoryList .pibBox {
  margin-right: 20px;
}

/* #category .categoryList .pibBox img {
    max-height: 80px;
  } */

#category .categoryList .pibBox .name {
  color: #b3a777;
  font-size: 18px;
}
</style>



