<template>
    <div id="firstCategory" class="flexAlignCenter">
        <ul class="textCenter">
            <li :class="item.isActive?'active':''" v-for="item,index in cateList" @click.stop="itemTap(item)" v-bind:key="index">
                <i :class="'iconfont '+item.icon "></i>
                <p class="cateName">{{item.name}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
  data() {
    return {
      cateList: [
          {
              icon:'icon-yishu',
              name:'艺术品',
              isActive:true
          },{
              icon:'icon-gongyipin',
              name:'工艺品',
              isActive:false
          }
      ]
    };
  },
  methods:{
      itemTap(item){
          for(var i in this.cateList){
              this.cateList[i].isActive=false;
          }
          item.isActive=true;
      }
  }
};
</script>

<style scoped>
#firstCategory {
  width: 30%;
  border-right: 1px solid #eee;
  justify-content: center;
}
#firstCategory ul {
  list-style: none;
  padding-left: 10px;
}
#firstCategory ul li {
  margin: 60px 0;
  font-size: 19px;
  color: #666;
}

#firstCategory i.iconfont {
  display: block;
  font-size: 28px;
}
#firstCategory ul li.active {
  color: #919077;
  font-weight: bold;
}
#firstCategory ul li.active i.iconfont {
  color: #7d6e30;
  font-weight: bold;
}
</style>



