<template>
  <div id="details">
    <div class="leftBox">
        <router-link class="Home" target="div" :to="{path:'category'}">Home</router-link>
      <fixText></fixText>
      <textLogo></textLogo>
    </div>
    <div class="gallery">
      <div class="content">
        <a :href="item.src" data-caption="Image caption" class="eachPic" v-for="item,index in picList">
          <img :src="item.src" alt="First image">
        </a>
      </div>

    </div>
  </div>
</template>

<script>
  let bBox = require("../../assets/js/baguetteBox.js");
  import fixText from "@/components/fixText";
  import textLogo from "@/components/textLogo";
  export default {
    data() {
      return {
        picList: [{
            src: require("../../assets/images/details/1.jpg")
          },
          {
            src: require("../../assets/images/details/2.jpg")
          },
          {
            src: require("../../assets/images/details/3.jpg")
          },
          {
            src: require("../../assets/images/details/4.jpg")
          },
          {
            src: require("../../assets/images/details/5.jpg")
          },
          {
            src: require("../../assets/images/details/6.jpg")
          }
        ]
      };
    },
    components: {
      fixText,
      textLogo
    },
    mounted() {
      bBox.baguetteBox.run(".gallery");
    }
  };
</script>

<style scoped>
  #details #baguetteBox-overlay .full-image img {
    width: 80% !important;
    height: 80% !important;
  }
  #details .Home{
    margin: 40px;
    font-size: 26px;
    color: #1861ae;
    padding: 0 10px;
    border-left: 3px solid #b5a476;
    width: auto;
    display: inline-block;
    border-right: 3px solid #b5a476;
    text-decoration: underline;
    
  }
  #details {
    display: flex;
    align-content: flex-start;
    width: 100%;
    height: 100%;
    background: #fcfafb;
  }

  #details .eachPic {
    max-width: 20%;
    max-height: 120px;
    margin: 15px;
    display: inline-block;
  }

  #details .leftBox {
    width: 30%;
    height: 100%;
  }

  #details .gallery {
    width: 70%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
   
  }
  #details .gallery .content{
    height: 90%;
    background: #f3f2f8;
    margin: 0 auto;
    background-clip: padding-box;
    width: 90%;
    overflow: scroll;
  }

  #details .eachPic img {
    width: 100%;
    height: 100px;
  }
</style>



